const Menu = () => {
  const $burgerMenuIcon = $('.js-burger');
  const $burgerMenu = $('.burger-menu');
  const $burgerCloseBtn = $('.js-burger-close');

  init();

  function init() {
    $burgerMenuIcon.on('click', showMenu);
    $burgerCloseBtn.on('click', hideMenu);
  }

  function showMenu() {
    $burgerMenu.slideDown(700);
  }

  function hideMenu() {
    $burgerMenu.slideUp(700);
  }

  if($('#js-scrollspy').length > 0) {
    var spy = new ScrollSpy(('#js-scrollspy'), {
      nav: '.js-scrollspy-nav > a',
      className: 'is-inview'
    });
  }
};


export default Menu;